  
  
.slider {
    width: 95%;
    margin: auto;
    overflow: hidden;
  }
  
  .slider ul {
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    width: 400%;
    -webkit-animation: slid 10s infinite reverse;
            animation: slid 10s infinite reverse;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    
  }
  
  .slider li {
    width: 50%;
    list-style: none;
    display: -ms-flexbox;
    display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-line-pack: center;
	    align-content: center;
  }
  
  .slider img {
    width: 50vw;
    height: 50vh;
  }
  
  @-webkit-keyframes slid {
    0% {margin-left: 0;}
    20% {margin-left: 0;}
    
    25% {margin-left: -100%;}
    45% {margin-left: -100%;}
    
    50% {margin-left: -200%;}
    70% {margin-left: -200%;}
    
    75% {margin-left: -300%;}
    100% {margin-left: -300%;}
  }
  
  @keyframes slid {
    0% {margin-left: 0;}
    20% {margin-left: 0;}
    
    25% {margin-left: -100%;}
    45% {margin-left: -100%;}
    
    50% {margin-left: -200%;}
    70% {margin-left: -200%;}
    
    75% {margin-left: -300%;}
    100% {margin-left: -300%;}
  }
  
